
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

export default {
name: 'headerT',
components: {
Flexbox,
FlexboxItem
},
data () {
return {
msg: 'Welcome to headerT',
titleList: [
{name: ''},
{name: ''},
{name: ''},
{name: ''},
{name: ''},
{name: ''},
{name: ''},
{name: ''},
{name: ''},
{name: ''},
{name: 'VR'},
{name: ''},
{name: ''},
{name: ''},
{name: 'Windows'},
],
uphide: '',                   // logo
titleparstyle: '',            // titlepar()
titleline: '',                // 
arrow: 'iconfont icon-down',  // 
arrowstyle: '',               // 
menutitle: '',                // 
menuclass: 'hidemenu',        // 
activetitle: '',  // 
}
},
created () {},
methods: {

showmenu (e) {
if(this.arrow.indexOf('down') > 0){
this.arrowstyle = 'transition:.3s ease;transform:rotate(180deg)';
this.titleparstyle = 'overflow-x:hidden;';

this.arrow = 'iconfont icon-up';
this.menuclass = 'showmenu';

}else{
this.arrowstyle = 'transition:.3s ease;transform:rotate(0deg)';
this.titleparstyle = 'overflow-x:scroll;';

this.arrow = 'iconfont icon-down';
this.menuclass = 'hidemenu';
}
},

clkTitle (item, e) {
var el = e.currentTarget;
console.log('width: ',this.getStyle(el, 'width'));
console.log('left: ',this.getStyle(el.parentNode, 'scrollWidth'));
this.activetitle = item;
this.titleline = 'width:' + this.getStyle(el, 'width')*8/10 + 'px;transform:translateX('+ this.getStyle(el, 'left') +'px)';
},

selectTitle (item) {

console.log('item: ',item);
console.log('typeof item: ',typeof item);
this.arrowstyle = 'transition:.3s ease;transform:rotate(0deg)';
this.titleparstyle = 'overflow-x:scroll;';
this.activetitle = item;
this.arrow = 'iconfont icon-down';
this.menuclass = 'hidemenu';

},

getStyle (el, attr) {
var style;
if(attr){
style = window.getComputedStyle(el, false)[attr];

}else{
style = window.getComputedStyle(el, false);
}
return style;
}
}
}



